import { loadImage } from 'sol-common';
import { SOL } from 'sol-common/util';
import D1JeiBot from '../components/D1JeiBot';
import D1Block from '../components/D1Block';

import backgroundImage from '../assets/images/background.svg';
import Snap from 'sol-common/snap';
import { wait } from '../utils/util';

export default function DM000001({ canvas = SOL.SVG, ...p }) {
    const g = canvas;
    const callback = p.callback;
    const backgroundG = g.g().addClass('D1-background');
    const blockG = g.g().addClass('D1-blocks');
    const jeiBotG = g.g().addClass('D1-jeibot');
    const blockG2 = g.g().addClass('D1-blocks2');

    let blocks;
    let jeibot;

    const drawBackground = () => {
        const loadBackgroundImage = loadImage({ src: backgroundImage, type: 'svg', key: 'D1-background' });
        loadBackgroundImage.use().appendTo(backgroundG);
        // const { x, y, scale, index: url } = p.backgroundImage;
        // image({ canvas: backgroundG, x, y, img: { url, scale } });
    };

    const drawBlock = () => {
        blocks = D1Block({ g: blockG, data: p.block });
    };

    const drawJeiBot = () => {
        const { startPosition, startDirection } = p.jeiBot;
        jeibot = D1JeiBot({ g: jeiBotG, data: blocks, startPosition, startDirection });
    };

    const init = () => {
        blocks.forEach(el => el.forEach(el => el !== 'empty' && el.init()));
        jeibot.init();
    };

    const jeiBotPlay = async () => {
        SOL.MESSAGEACTIVE = false;
        const tiles = SOL.BOARDCONTAINER.children();
        tiles.forEach(el => {
            el.children()[0].attr({ opacity: 1 });
            el.children()[1].attr({ opacity: 0 });
        });
        let isAnswerCheck = true;
        let userAction;
        if (SOL.USERTILE.length === 0) {
            userAction = [
                { type: 'front', index: 1 },
                { type: 'crush', index: 1 },
                { type: 'front', index: 1 },
                { type: 'front', index: 1 },
                { type: 'front', index: 1 },
                { type: 'rotateR', index: 1 },
                { type: 'front', index: 1 },
                { type: 'rotateR', index: 1 },
                { type: 'rotateR', index: 1 },
                { type: 'front', index: 1 }                
            ];
        } else {
            userAction = SOL.USERTILE.reduce((p, c, i) => {
                if (c === 'front3') {
                    for (let index = 0; index < 3; index++) {
                        p.push({ type: 'front', index: i });
                    }
                } else p.push({ type: c, index: i });
                return p;
            }, []);
        }

        for (let i = 0; i < userAction.length; i++) {
            const action = userAction[i].type;
            const targetIndex = userAction[i].index;
            if (tiles[targetIndex]) {
                tiles[targetIndex].children()[0].attr({ opacity: 0 });
                tiles[targetIndex].children()[1].attr({ opacity: 1 });
            }

            if (action === 'front') {
                const [row, column] = jeibot.getNextTilePosition({ moveType: 'F' });
                if (row < 0 || column < 0 || !blocks[row] || !blocks[row][column] || blocks[row][column] === 'empty') {
                    await moveAnimErrorHandler('front');
                    isAnswerCheck = false;
                    break;
                }
                if (blocks[row][column].status === 2) {
                    await jeibot.heading({ row, column });
                    await moveAnimErrorHandler('crush');
                    isAnswerCheck = false;
                    break;
                }

                if (blocks[row][column].status === 9) {
                    flagAnimate({ type: 1, element: blocks[row][column].flag });
                } else {
                    if (blocks[row][column].flag.attr('opacity') !== '1') flagAnimate({ type: 2, element: blocks[row][column].flag });
                }

                await jeibot.move({ row, column });
            }

            if (action === 'crush') {
                const [row, column] = jeibot.getNextTilePosition({ moveType: 'F' });
                const duration = SOL.soundController.crushIce.duration() * 1000;
                const blockStats = blocks[row] && blocks[row][column] && blocks[row][column].status;
                let animate;
                if (blockStats === 2) {
                    animate = [jeibot, blocks[row][column]];
                } else {
                    animate = [jeibot];
                }
                await Promise.all(animate.map(el => el.crush({ dealy: duration, blockStats })));
            }

            if (action === 'rotateR') {
                await jeibot.rotate({ type: 'R' });
            }

            if (action === 'rotateL') {
                await jeibot.rotate({ type: 'L' });
            }
            if (tiles[targetIndex]) {
                tiles[targetIndex].children()[0].attr({ opacity: 1 });
                tiles[targetIndex].children()[1].attr({ opacity: 0 });
                tiles[targetIndex].children()[0].node.scrollIntoView({ block: 'center' });
            }
        }

        tiles.forEach(el => {
            el.children()[0].attr({ opacity: 0 });
            el.children()[1].attr({ opacity: 1 });
        });

        isAnswerCheck && answerCheck();
    };
    const flagAnimate = ({ type, element }) => {
        const opacity = 0;
        if (type === 1) {
            Snap.animate(0, 1, val => element.attr({ opacity: 1 - (1 - opacity) * val }), 1000);
        }
        if (type === 2) {
            Snap.animate(0, 1, val => element.attr({ opacity: (1 - opacity) * val + opacity }), 1000);
        }
    };

    const moveAnimErrorHandler = async type => {
        // 갈 길이 없는 경우 : 3번 케이스
        // 얼음 앞에서 앞으로 갈 경우 : 4번 케이스
        await SOL.popupController.tryAgain({ type });
        init();
        console.log(SOL.USERTILE);
    };

    const answerCheck = async () => {
        const { position } = jeibot.getCurrentStatus();
        const [row, column] = position;
        const { status } = blocks[row][column];

        if (status === 9) {
            // 목표 지점에 도달한 경우 : 2번 케이스
            await wait(500);
            blocks[row][column].end();
            await jeibot.flag();
            SOL.popupController.greatJob();
        } else {
            // 목표 지점에 도달하지 못한 경우 : 8번 케이스
            await wait(500);
            await SOL.popupController.tryAgain({ type: 'wrong' });
            init();
            console.log(SOL.USERTILE);
        }
    };

    const rearrangeBlock = () => {
        const { startPosition } = p.jeiBot;
        blocks.forEach((el, i) => {
            if (i > startPosition[0])
                el.forEach(el => {
                    if (el.status === 9 || el.status === 2) {
                        el.node.children()[1].appendTo(blockG2);
                    }
                });
        });
    };

    const start = async () => {
        drawBackground();
        drawBlock();
        drawJeiBot();
        rearrangeBlock();
        SOL.PLAY = jeiBotPlay;        

        SOL.soundController.background.loop = true;
        SOL.soundController.background.play();
        g.rect(0, 0, 100, 100)
            .attr({ opacity: 0 })
            .click(() => {
                SOL.soundController.background.play();
            });
    };

    start();
}
