// startDirection : 'R', 'L', 'U', 'D'
import Snap from 'sol-common/snap';
import { wait } from '../utils/util';

import jeibotR from '../assets/images/jeibot/jeibotR.svg';
import jeibotMoveR from '../assets/images/jeibot/jeibotMoveR.svg';
import jeibotCrushIceR from '../assets/images/jeibot/jeibotCrushIceR.svg';
import jeibotCrushR from '../assets/images/jeibot/jeibotCrushR.svg';
import jeibotHeadingR from '../assets/images/jeibot/jeibotHeadingR.svg';
import jeibotFlagR from '../assets/images/jeibot/jeibotFlagR.svg';

import jeibotL from '../assets/images/jeibot/jeibotL.svg';
import jeibotMoveL from '../assets/images/jeibot/jeibotMoveL.svg';
import jeibotCrushIceL from '../assets/images/jeibot/jeibotCrushIceL.svg';
import jeibotCrushL from '../assets/images/jeibot/jeibotCrushL.svg';
import jeibotHeadingL from '../assets/images/jeibot/jeibotHeadingL.svg';
import jeibotFlagL from '../assets/images/jeibot/jeibotFlagL.svg';

import jeibotU from '../assets/images/jeibot/jeibotU.svg';
import jeibotMoveU from '../assets/images/jeibot/jeibotMoveU.svg';
import jeibotCrushIceU from '../assets/images/jeibot/jeibotCrushIceU.svg';
import jeibotCrushU from '../assets/images/jeibot/jeibotCrushU.svg';
import jeibotHeadingU from '../assets/images/jeibot/jeibotHeadingU.svg';
import jeibotFlagU from '../assets/images/jeibot/jeibotFlagU.svg';

import jeibotD from '../assets/images/jeibot/jeibotD.svg';
import jeibotMoveD from '../assets/images/jeibot/jeibotMoveD.svg';
import jeibotCrushIceD from '../assets/images/jeibot/jeibotCrushIceD.svg';
import jeibotCrushD from '../assets/images/jeibot/jeibotCrushD.svg';
import jeibotHeadingD from '../assets/images/jeibot/jeibotHeadingD.svg';
import jeibotFlagD from '../assets/images/jeibot/jeibotFlagD.svg';

import { loadImage } from 'sol-common';
import { SOL } from 'sol-common/util';

const loadJeibotR = loadImage({ src: jeibotR, key: 'D1-jeibot-R', type: 'svg' });
const loadJeibotL = loadImage({ src: jeibotL, key: 'D1-jeibot-L', type: 'svg' });
const loadJeibotU = loadImage({ src: jeibotU, key: 'D1-jeibot-U', type: 'svg' });
const loadJeibotD = loadImage({ src: jeibotD, key: 'D1-jeibot-D', type: 'svg' });

export default function D1JeiBot({ g, data, startPosition, startDirection }) {
    const [row, column] = startPosition;
    const initData = data[row][column];
    const jeiBotStatus = {
        currentDirection: startDirection,
        currentPosition: startPosition
    };

    const JeiBot = () => {
        const { point } = initData;
        g.transform('t' + [point.x, point.y]);
        const { w, h } = loadJeibotR.getBBox();
        const transform = 't' + [-w / 2, -h - 20];

        g.show = async ({ type }) => {
            g.clear();
            switch (type) {
                case 'R':
                    loadJeibotR.use().transform(transform).appendTo(g);

                    break;
                case 'flagR':
                    loadImage({ src: jeibotFlagR, key: 'D1-jeibot-falgR', type: 'svg' })
                        .use()
                        .transform(transform + 't23.5, 21.5 s1.46')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-flagR'];
                    break;
                case 'moveR':
                    loadImage({ src: jeibotMoveR, key: 'D1-jeibot-moveR', type: 'svg' })
                        .use()
                        .transform(transform + 't-17.5, -20')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-moveR'];
                    break;
                case 'crushIceR':
                    loadImage({ src: jeibotCrushIceR, key: 'D1-jeibot-crushIceR', type: 'svg' })
                        .use()
                        .transform(transform + 't-95, -100')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-crushIceR'];
                    break;
                case 'crushR':
                    loadImage({ src: jeibotCrushR, key: 'D1-jeibot-crushR', type: 'svg' })
                        .use()
                        .transform(transform + 't-95, -100')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-crushR'];
                    break;
                case 'headingR':
                    loadImage({ src: jeibotHeadingR, key: 'D1-jeibot-headingR', type: 'svg' })
                        .use()
                        .transform(transform + 't-70, -20')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-headingR'];
                    break;
                case 'L':
                    loadJeibotL.use().transform(transform).appendTo(g);
                    break;
                case 'flagL':
                    loadImage({ src: jeibotFlagL, key: 'D1-jeibot-falgL', type: 'svg' })
                        .use()
                        .transform(transform + 't5, 20 s1.47')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-flagL'];
                    break;
                case 'moveL':
                    loadImage({ src: jeibotMoveL, key: 'D1-jeibot-moveL', type: 'svg' })
                        .use()
                        .transform(transform + 't-17.5, -20')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-moveL'];
                    break;
                case 'crushIceL':
                    loadImage({ src: jeibotCrushIceL, key: 'D1-jeibot-crushIceL', type: 'svg' })
                        .use()
                        .transform(transform + 't-202.5, -100')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-crushIceL'];
                    break;
                case 'crushL':
                    loadImage({ src: jeibotCrushL, key: 'D1-jeibot-crushL', type: 'svg' })
                        .use()
                        .transform(transform + 't-172.5, -100')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-crushL'];
                    break;
                case 'headingL':
                    loadImage({ src: jeibotHeadingL, key: 'D1-jeibot-headingL', type: 'svg' })
                        .use()
                        .transform(transform + 't-70, -20')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-headingL'];
                    break;
                case 'U':
                    loadJeibotU.use().transform(transform).appendTo(g);
                    break;
                case 'moveU':
                    loadImage({ src: jeibotMoveU, key: 'D1-jeibot-moveU', type: 'svg' })
                        .use()
                        .transform(transform + 't13, 10 s1.45')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-moveU'];
                    break;
                case 'crushIceU':
                    loadImage({ src: jeibotCrushIceU, key: 'D1-jeibot-crushIceU', type: 'svg' })
                        .use()
                        .transform(transform + 't35, 20 s1.5')
                        .appendTo(g);
                    break;
                case 'crushU':
                    loadImage({ src: jeibotCrushU, key: 'D1-jeibot-crushU', type: 'svg' })
                        .use()
                        .transform(transform + 't33, 18 s1.46')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-crushU'];
                    break;
                case 'headingU':
                    loadImage({ src: jeibotHeadingU, key: 'D1-jeibot-headingU', type: 'svg' })
                        .use()
                        .transform(transform + 't13, 13 s1.45')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-headingU'];
                    break;
                case 'flagU':
                    loadImage({ src: jeibotFlagU, key: 'D1-jeibot-falgU', type: 'svg' })
                        .use()
                        .transform(transform + 't-60, 15 s1.46')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-flagU'];
                    break;
                case 'D':
                    loadJeibotD.use().transform(transform).appendTo(g);
                    break;
                case 'moveD':
                    loadImage({ src: jeibotMoveD, key: 'D1-jeibot-moveD', type: 'svg' })
                        .use()
                        .transform(transform + 't4, 10 s1.45')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-moveD'];
                    break;
                case 'crushD':
                    loadImage({ src: jeibotCrushD, key: 'D1-jeibot-crushD', type: 'svg' })
                        .use()
                        .transform(transform + 't-152, -105')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-crushD'];
                    break;
                case 'crushIceD':
                    loadImage({ src: jeibotCrushIceD, key: 'D1-jeibot-crushIceD', type: 'svg' })
                        .use()
                        .transform(transform + 't-155, -100')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-crushIceD'];
                    break;
                case 'headingD':
                    loadImage({ src: jeibotHeadingD, key: 'D1-jeibot-headingD', type: 'svg' })
                        .use()
                        .transform(transform + 't15, 10 s1.45')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-headingD'];
                    break;
                case 'flagD':
                    loadImage({ src: jeibotFlagD, key: 'D1-jeibot-falgD', type: 'svg' })
                        .use()
                        .transform(transform + 't-25, -30')
                        .appendTo(g);
                    delete SOL.imageDefs['D1-jeibot-flagD'];
                    break;
                default:
                    break;
            }
        };

        return g;
    };

    const init = async () => {
        const { x, y } = initData.point;
        jeibot.transform('t' + [x, y]);
        jeibot.show({ type: startDirection });

        jeiBotStatus.currentDirection = startDirection;
        jeiBotStatus.currentPosition = startPosition;
    };

    const move = async ({ row, column }) => {
        const { direction } = getCurrentStatus();
        const { e, f } = jeibot.transform().localMatrix;
        const { x, y } = data[row][column].point;
        const [dx, dy] = [x - e, y - f];
        const pathData = `M${e}, ${f} L${e + dx}, ${f + dy}`;
        const sound = SOL.soundController.move;
        const duration = 1200;
        sound.play();
        jeibot.show({ type: 'move' + direction });
        await moveAnimate({ pathData, duration, element: g });
        jeibot.show({ type: direction });

        jeiBotStatus.currentPosition = [row, column];
    };

    const heading = async ({ row, column }) => {
        const { direction } = getCurrentStatus();
        const { e, f } = jeibot.transform().localMatrix;
        const { x, y } = data[row][column].point;
        const [dx, dy] = [(x - e) * 0.4, (y - f) * 0.4];
        const pathData = `M${e}, ${f} L${e + dx}, ${f + dy}`;
        const sound = SOL.soundController.heading;
        const duration = 1000;

        jeibot.show({ type: 'heading' + direction });
        await moveAnimate({ pathData, duration, element: g });
        await wait(350);
        sound.play();
        await wait(2000);
    };

    const rotate = async ({ type }) => {
        const sound = SOL.soundController.rotate;
        const duration = sound.duration() * 1000;

        sound.play();
        setCurrentDirection({ rotateType: type });
        jeibot.show({ type: jeiBotStatus.currentDirection });

        await new Promise(resolve => setTimeout(resolve, duration));
    };

    const crush = async ({ blockStats }) => {
        const { direction } = getCurrentStatus();
        const crushIce = SOL.soundController.crushIce;
        const crush = SOL.soundController.crush;
        const duration = blockStats === 2 ? crushIce.duration() * 1000 : 1000;
        const type = blockStats === 2 ? 'crushIce' + direction : 'crush' + direction;

        jeibot.show({ type });
        if (blockStats === 2) {
            await wait(800);
            crushIce.play();
        } else {
            await wait(500);
            crush.play();
        }
        await wait(duration);
        jeibot.show({ type: direction });
    };

    const flag = async () => {
        const { direction } = getCurrentStatus();
        const greatJobSound = SOL.soundController.greatJob;
        const duration = greatJobSound.duration();
        jeibot.show({ type: 'flag' + direction });
        greatJobSound.play();
        await wait(duration * 1000 + 500);
    };

    const moveAnimate = ({ pathData, duration, element, easing }) => {
        const startPt = Snap.path.getPointAtLength(pathData, 0);
        const totalLength = Snap.path.getTotalLength(pathData);
        const { local } = element.transform();
        return new Promise(resolve => {
            const anim = Snap.animate(
                0,
                1,
                val => {
                    const { x, y } = Snap.path.getPointAtLength(pathData, val * totalLength);
                    const transform = local + 't' + [x - startPt.x, y - startPt.y];
                    element.transform(transform);
                },
                duration,
                easing,
                () => {
                    resolve();
                    anim.stop();
                }
            );
        });
    };

    const getCurrentStatus = () => {
        const { currentDirection, currentPosition } = jeiBotStatus;
        return { direction: currentDirection, position: currentPosition };
    };

    const setCurrentDirection = ({ rotateType }) => {
        const { direction } = getCurrentStatus();

        switch (direction) {
            case 'R':
                if (rotateType === 'R') jeiBotStatus.currentDirection = 'D';
                if (rotateType === 'L') jeiBotStatus.currentDirection = 'U';
                break;
            case 'L':
                if (rotateType === 'R') jeiBotStatus.currentDirection = 'U';
                if (rotateType === 'L') jeiBotStatus.currentDirection = 'D';
                break;
            case 'U':
                if (rotateType === 'R') jeiBotStatus.currentDirection = 'R';
                if (rotateType === 'L') jeiBotStatus.currentDirection = 'L';
                break;
            case 'D':
                if (rotateType === 'R') jeiBotStatus.currentDirection = 'L';
                if (rotateType === 'L') jeiBotStatus.currentDirection = 'R';
                break;
            default:
                break;
        }
    };

    const getNextTilePosition = ({ moveType }) => {
        const { direction, position } = getCurrentStatus();
        const targetPosition = position.map(el => el);
        switch (direction) {
            case 'R':
                if (moveType === 'F') targetPosition[1] += 1;
                if (moveType === 'B') targetPosition[1] -= 1;
                break;
            case 'L':
                if (moveType === 'F') targetPosition[1] -= 1;
                if (moveType === 'B') targetPosition[1] += 1;
                break;
            case 'U':
                if (moveType === 'F') targetPosition[0] -= 1;
                if (moveType === 'B') targetPosition[0] += 1;
                break;
            case 'D':
                if (moveType === 'F') targetPosition[0] += 1;
                if (moveType === 'B') targetPosition[0] -= 1;
                break;
            default:
                break;
        }
        return targetPosition;
    };

    const jeibot = JeiBot();
    jeibot.show({ type: jeiBotStatus.currentDirection });

    return { init, move, heading, rotate, crush, flag, getNextTilePosition, getCurrentStatus };
}
