import { getPointInMatrixForm, wait } from '../utils/util';
import { loadImage } from 'sol-common';
import block from '../assets/images/block.svg';
import iceBreaking from '../assets/images/iceBreaking.svg';
import ice from '../assets/images/ice.svg';
import flag from '../assets/images/flag.svg';

const loadBlock = loadImage({ src: block, key: 'D1-block', type: 'svg' });
const loadIce = loadImage({ src: ice, key: 'D1-ice', type: 'svg' });
const loadFlag = loadImage({ src: flag, key: 'D1-flag', type: 'svg' });

export default function D1Block({ g, data } = {}) {
    const points = getPointInMatrixForm({ startPoint: { x: 720, y: 780 }, rows: data.length, columns: data[0].length, gap: { x: 225, y: 110 } });
    const tiles = [];

    data.forEach((el, i) => {
        tiles[i] = [];
        el.forEach((el2, j) => {
            if (el2 > 0) {
                const { x, y } = points[i][j];
                const tileG = g.g().addClass('tile');
                let crush = async ({ dealy }) => await wait(dealy);
                let init = () => {
                    tiles[i][j].status = el2;
                };
                let end = () => {};
                const { w, h } = loadBlock.getBBox();
                const transform = 't' + [x - w / 2, y - h / 2];
                loadBlock.use().transform(transform).appendTo(tileG);

                if (el2 === 2) {
                    const { w, h } = loadIce.getBBox();
                    const transform = 't' + [x - w / 2, y - h];
                    const ice = loadIce.use().transform(transform).appendTo(tileG);                    

                    crush = async () => {
                        await wait(1000);
                        ice.attr({ opacity: 0 });
                        loadImage({ src: iceBreaking, key: 'D1-ice-breaking', type: 'svg' })
                            .use()
                            .transform(transform + 't-175, -80')
                            .appendTo(tileG);
                        tiles[i][j].status = 1;
                    };

                    init = () => {
                        tiles[i][j].status = el2;
                        ice.attr({ opacity: 1 });
                    };
                }
                if (el2 === 9) {
                    const { w, h } = loadFlag.getBBox();
                    const transform = 't' + [x - w / 2 + 25, y - h - 25];
                    loadFlag.use().transform(transform).appendTo(tileG);
                    init = () => {                        
                        loadFlag.attr({ opacity: 1 });
                    };
                    end = () => {
                        loadFlag.attr({ opacity: 0 });
                    };
                }

                tiles[i][j] = { node: tileG, status: el2, point: { x, y }, init, crush, end, flag: loadFlag };
            } else {
                tiles[i][j] = 'empty';
            }
        });
    });

    return tiles;
}
